
// pagecurrent
const SET_PAGECURRENT = 'SET_PAGECURRENT';

// datalist()
const SET_DATALIST = 'SET_DATALIST';

// limit*()
const SET_LIMIT= 'SET_LIMIT';

// tab
const SET_TABS = 'SET_TABS';

// goValue
const SET_GOVALUE = 'SET_GOVALUE';

//id
const SET_IDLIST = 'SET_IDLIST'

// id
const SET_ID = 'SET_ID'


//action creators
export const setPageCurrent = (pageCurrent) => {
    return {
        type : SET_PAGECURRENT,
        pageCurrent,
    };
};

export const setDataList = (dataList) => {
    return {
        type : SET_DATALIST,
        dataList,
    }
};
export const setLimit = function(limit) {
    return {
        type : SET_LIMIT,
        limit,
    };
};
export const setTabs = function(tab) {
    return {
        type : SET_TABS,
        tab,
    };
};
export const setGoValue = function(value) {
    return {
        type : SET_GOVALUE,
        govalue:value,
    };
};
export const setIdList = function(idList) {
    return {
        type : SET_IDLIST,
        idList,
    };
};
export const setId = function(id) {
    return {
        type : SET_ID,
        id,
    };
};


